import React from 'react'
import styled from 'styled-components'
import TranslatedText from '../TranslatedText'

const Nav: React.FC = () => {
  return (
    <StyledNav>
      <StyledAbsoluteLink href="/#/swap?outputCurrency=0xbfa403d0e5dc194b14a5e8da05d44a2986d1399d" className="active">
        <TranslatedText translationId={116}>Exchange</TranslatedText>
      </StyledAbsoluteLink>
      {/* <StyledAbsoluteLink href="/#/farm">
        <TranslatedText translationId={256}>Farms</TranslatedText>
      </StyledAbsoluteLink>
  */ }
      <StyledAbsoluteLink href="https://nft.foex.cc/">
        <TranslatedText translationId={258}>NFT</TranslatedText>
      </StyledAbsoluteLink>
      <StyledAbsoluteLink href="https://info.foex.cc/">
        <TranslatedText translationId={260}>Info</TranslatedText>
      </StyledAbsoluteLink>
      <StyledAbsoluteLink href="/#/airdrop">
        <TranslatedText translationId={262}>AirDrop</TranslatedText>
      </StyledAbsoluteLink>
    </StyledNav>
    
  )
}

const StyledNav = styled.nav`
  align-items: center;
  display: flex;
  font-size: 14px;
  font-weight: 700;
`

const StyledAbsoluteLink = styled.a`
  position: relative;
  color: rgb(127, 134, 143);
  margin-right: 24px;
  text-decoration: none;
  &:hover {
    color: ${({ theme }) => theme.colors.primary}
  }
  @media (max-width: 400px) {
    margin-right: 24px;
  }
`
export default Nav
