import React, { createContext, useEffect, useState, useCallback } from 'react'
import BigNumber from 'bignumber.js'
import multicall from '../utils/multicall'
import erc20 from '../constants/abis/erc20.json'
import { getBalanceNumber } from '../utils/formatBalance'
import { useBlock } from '../hooks/useBlock'

export interface PriceContext {
  htPrice: number
  focPrice: number
  liquidity: string
  marketCap: string
}

export const Context = createContext<PriceContext>({
  htPrice: 0,
  focPrice: 0,
  liquidity: "0",
  marketCap: "0"
})

const PriceProvider: React.FC = ({ children }) => {
  const block = useBlock()
  const [price, setPrice] = useState({
    htPrice: 0,
    focPrice: 0,
    liquidity:"0",
    marketCap:"0"
  })

  const fetchBalance = useCallback(async () => {
    const calls = [
      {
        address: '0x0298c2b32eae4da002a15f36fdf7615bea3da047',
        name: 'balanceOf',
        params: ['0x2129e956d7157ffbcfa65abbab3c66c9456dba0d'],
      },
      {
        address: '0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f',
        name: 'balanceOf',
        params: ['0x2129e956d7157ffbcfa65abbab3c66c9456dba0d'],
      },
      /*{
        address: '0xb62E3b6a3866f5754FdeFcf82e733310e2851043',
        name: 'balanceOf',
        params: ['0xFe47128Fc5F1591325d2D9a255EEc5D2AFB57491']
      },*/
      {
        address: '0xbfa403d0e5dc194b14a5e8da05d44a2986d1399d',
        name: 'balanceOf',
        params: ['0x6831132af725e9fe7873c52f50cc70d1d6b45ca9']
      },
      {
        address: '0xa71edc38d189767582c38a3145b5873052c3e47a',
        name: 'balanceOf',
        params: ['0x6831132af725e9fe7873c52f50cc70d1d6b45ca9'],
      },{
        address:"0xbfa403d0e5dc194b14a5e8da05d44a2986d1399d",
      name:"balanceOf",
      params:["0x000000000000000000000000000000000f0cdead"]
     },{
      address: '0xbfa403d0e5dc194b14a5e8da05d44a2986d1399d',
      name: 'balanceOf',
      params: ['0x737b4fccda48bfe74d3de631aacca89d6f94f0e4']
    },
    {
      address: '0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f',
      name: 'balanceOf',
      params: ['0x737b4fccda48bfe74d3de631aacca89d6f94f0e4'],
    }
    ]
    try {
      const [busd, ht, foc, usdt,blackhole,foc1,ht1] = await multicall(erc20, calls)
      const htPrice = getBalanceNumber(new BigNumber(busd), 8) / getBalanceNumber(new BigNumber(ht))
      const focht = getBalanceNumber(new BigNumber(usdt)) / getBalanceNumber(new BigNumber(foc),18)
      const focPrice = focht //* htPrice
      const liquiditySrc=(getBalanceNumber(new BigNumber(usdt))*1 + getBalanceNumber(new BigNumber(foc),18)*focPrice)
      const liquiditySrc1=(getBalanceNumber(new BigNumber(foc1),18)*focPrice)+ (getBalanceNumber(new BigNumber(ht1))*htPrice)
      const liquiditySrc2=liquiditySrc+liquiditySrc1
      const liquidity=liquiditySrc2.toFixed(2)
      const f=(2048*1e8)-getBalanceNumber(new BigNumber(blackhole),18)
      const marketCap=((f*focPrice)+liquiditySrc2).toFixed(2)
      //console.log("busd, bnb0, cake, bnb1:",busd, bnb0, cake, bnb1,htmoon);
      //console.log("htPrice,pippiPrice,liquidity,marketCap:",htPrice,pippiPrice.toFixed(18),liquidity,marketCap);
      setPrice({
        htPrice,
        focPrice,
        liquidity,
        marketCap
      })
    } catch (error) {
      console.log('error..', error)
    }
  }, [])

  useEffect(() => {
    fetchBalance()
  }, [setPrice, block, fetchBalance])

  return <Context.Provider value={{ ...price }}>{children}</Context.Provider>
}

export default PriceProvider
